@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
header{width:100%; height:auto; background:#f4c34a; color:#fff; position: fixed;z-index: 1;}
header .center{display:flex; justify-content: space-between; padding:20px 0; align-items: center;}
header .logo{width: 60px;}
header .logo img {width: 100%;}
header .menu{width:30px; height:30px; border:1px solid #fff; display:flex; align-items: center; justify-content: center;}
header .menu i.fa{font-size:22px; color:#fff}
*{margin:0; padding:0; box-sizing: border-box;}
ol, ul{list-style:none;}
a {text-decoration: none; color: #333;}
body{font-family: 'Roboto', sans-serif; font-size:14px;}
.center{width:90%; height:auto; margin:auto;}
.App{display:flex; width:100%; height:auto; flex-direction: column;}
.App .home_search{position: relative; -webkit-transform: translateY(3%); transform: translateY(3%);}
.App .search_form .center{width:100%; height:auto; display:flex; justify-content: center; padding:30px 0; }
.App .search_form input{height: 40px; padding:5px; width:70%; background: #f1f1f1; border-radius: 10px; border: 0;}
.App .search_form button{width:10%; background:#f4c34a; color:#fff; border:0; margin-left:5px; border-radius: 10px;}
.App .recipes {width: 100%; height: auto; text-align: center;}
.App .recipes>div {padding: 40px 0 0 0; width: 80%; margin: 40px auto; border-radius: 30px; box-shadow: 0px 0px 11px 6px rgb(131 131 131 / 10%);}
.App .recipes .imgbox{position: relative; width: 300px; height:300px; margin: auto; overflow: hidden;}
.App .recipes .overlay{width: 100%; height: 100vh; background: rgba(0,0,0,0.3); position: absolute; top: 0; left: 0; transition: all 0.4s; display: none;}
.App .recipes .imgbox:hover .overlay {display: block;}
.App .recipes .imgbox img {width: 100%; transition: all 0.4s;}

.App .recipes .imgbox:hover img {-webkit-transform: scale(1.1);transform: scale(1.1);}

.App .recipes h2{padding: 10px 0;}
.App .recipes p {padding-bottom: 20px;}

.Loader {width: 100%; height: 100vh; position: fixed; top: 0; left: 0; display: flex; align-items: center; justify-content: center;}
.Loader img {width: 40px;}

.recipeDetail {width: 100%; height: auto;}
.recipeDetail img {width: 100vw;  position: fixed;}
.recipeDetail .ingrsbox {width: 100%; height: 500px; background: #fff; position: relative; z-index: 2; border-radius: 35px 35px 0 0; -webkit-transform: translateY(100%); transform: translateY(100%);}
.recipeDetail .ingrsbox .ingrs_txt {padding: 30px;}
.recipeDetail .ingrs_txt h2 {font-size: 35px; padding-bottom: 14px;}
.recipeDetail .ingrs_txt ul li {padding: 5px 0;}
.recipeDetail .ingrs_txt ul li i {color: #f4c34a; padding-right: 10px;}
